import React, { Component } from "react";
import {
  Navbar,
  NavbarBrand,
  Nav,
  NavbarToggler,
  Collapse,
  NavItem,
  Jumbotron,
} from "reactstrap";
import { NavLink } from "react-router-dom";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false,
    };
    this.toggleNav = this.toggleNav.bind(this); // bind() để toggleNav có thể gọi được bên trong render
  }

  //responsive của Navbar
  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen, //khi toggleNav được click, giá trị của nó luôn luôn khác isOpen
    });
  }
  render() {
    return (
      <React.Fragment>
        <Navbar dark expand="md" className="sticky-top">
          <div className="container">
            <NavbarToggler onClick={this.toggleNav} />
            <NavbarBrand className="mr-auto" href="/">
              <img
                src="assets/images/logo.png"
                height="50"
                width="130"
                alt="Chill..."
              />
            </NavbarBrand>
            <Collapse isOpen={this.state.isNavOpen} navbar>
              <Nav navbar>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-5 mr-3 text-dark"
                    to="/home"
                  >
                    <span className="fa fa-home fa-lg"></span> Trang chủ
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/menu"
                  >
                    <span className="fa fa-users fa-lg"></span> Nổi bật
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/departments"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-cutlery fa-lg"></span> Danh mục
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/detailmenu"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-coffee fa-lg"></span> Thực đơn
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/about"
                  >
                    <span className="fa fa-file-text fa-lg"></span> Giới thiệu
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/contact"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-volume-control-phone fa-lg"></span>
                    Liên Hệ
                  </NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </div>
        </Navbar>

        <Jumbotron className="bannerimg">
          <div className="container">
            <div className="row row-header">
              <div className="col-12 col-sm-6 text-bannerimg">
                <h1>Ristorante Con Fusion</h1>
                <i>
                  <p>
                    Cuộc đời như một tách trà......
                    <br />
                    ..... tất nhiên sẽ không đắng cả đời,
                    <br /> Nhưng phải trải qua dư vị đó mới cảm nhận được vị
                    ngọt.
                  </p>
                </i>
              </div>
            </div>
          </div>
        </Jumbotron>
        <marquee>
          <b className="text-danger">THÔNG BÁO:</b> Bắt đầu từ ngày 15/9/2021
          Cửa hàng <b className="text-danger">trở lại hoạt động bình thường</b>{" "}
          vì vậy rất mong nhận được sự ủng hộ từ các tín đồ trà sữa.{"   "}
          <b className="text-danger">
            <i>
              Cửa hàng vẫn tiếp tục FREE SHIP cho các đơn hàng từ 99k trong phạm
              vi 15KM. Xin cảm ơn!
            </i>
          </b>
        </marquee>
      </React.Fragment>
    );
  }
}
export default Header;



