import React from "react";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from "reactstrap";

//item slideshow
const items = [
  {
    src: "/images/a.jpg",
    altText: "Slide 1",
    caption: "Hãy nhớ!",
    captions: "Trà sữa phải có trân châu..... Bạn bè thêm chút vì nhau mới bền",
  },
  {
    src: "/images/b.jpg",
    altText: "Slide 2",
    caption: "Có những lúc",
    captions: "Anh ơi một trà sữa vị dâu, không “chân trâu” và nhiều thạch.",
  },
  {
    src: "/images/c.jpg",
    altText: "Slide 3",
    caption: "Đôi khi!",
    captions:
      "...1 ngày có 24h, Trong vòng 1h phải có 1 ly trà sữa, 23h còn lại suy nghĩ xem ngày mai uống vị gì",
  },
];

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0 };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === items.length - 1
        ? 0
        : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === 0
        ? items.length - 1
        : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  render() {
    const { activeIndex } = this.state;

    const slides = items.map((item) => {
      return (
        <CarouselItem
          className="carouselImg"
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img src={item.src} alt={item.altText} />
          <CarouselCaption
            captionText={item.captions}
            captionHeader={item.caption}
          />
        </CarouselItem>
      );
    });

    return (
      <div className="row">
        <div className="col-12">
          <div className="row">
            <div className="col-12 col-md-4 mb-5 ml-3">
              <img src="/assets/images/sttTrasua2.jpg" width="300px" alt="" />
            </div>
            <div className="col-12 col-md-7">
              <p style={{ fontSize: "25px" }}>
                <i>
                  Trà sữa thức uống pha trộn giữa trà và sữa kết hợp thêm các
                  loại trân châu, thạch tạo cảm giác mát lạnh, sảng khoái. Người
                  bình thường 60% cơ thể người là nước, riêng với tín đồ trà sữa
                  có lẽ 80% trọng lượng cơ thể dùng để chứa trà sữa.
                </i>
              </p>
            </div>
          </div>
        </div>
        <Carousel
          className="col-12 slides"
          activeIndex={activeIndex}
          next={this.next}
          previous={this.previous}
        >
          <CarouselIndicators
            items={items}
            activeIndex={activeIndex}
            onClickHandler={this.goToIndex}
          />
          {slides}
          <CarouselControl
            direction="prev"
            directionText="Previous"
            onClickHandler={this.previous}
          />
          <CarouselControl
            direction="next"
            directionText="Next"
            onClickHandler={this.next}
          />
        </Carousel>
        <div className="col-12">
          <div className="row  mt-5 mb-5">
            <div className="col-12 col-md-7 ml-3">
              <p style={{ fontSize: "25px" }}>
                <i>
                  Thanh xuân chưa gấu cũng như uống trà sữa không có trân châu.
                  Có người cần có người không nhưng đôi khi uống trà sữa không
                  trân trâu vẫn ngon như thường.
                </i>
              </p>
            </div>
            <div className="col-12 col-md-4">
              <img src="/assets/images/sttTrasua1.jpg" width="350px" alt="" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;



